@tailwind base;
@tailwind components;
@tailwind utilities;

header ul li{
    @apply px-4;
}

.bg-primary{
background-color: rgb(85 81 227);
}

.bg-secondary{
    background-color: rgb(67, 31, 150);

}
.mobile-nav{
    @apply fixed top-12 left-0 bg-gray-800 w-full h-1/4;
}



.mobile-nav li{
    @apply w-full h-full text-center border-b-2;
}
    
.project-desc{
    @apply absolute left-0 right-0 -bottom-0 top-0  bg-secondary opacity-0 duration-500 hover:opacity-100;
     
}

html{
    scroll-behavior: smooth;
}

